import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
// import { QueryVhweb, FirebaseAuth, OrdinaryComponent, OrdinaryAlgorithm } from 'private-angular-vhframeworks';
import { AddSalesmanClassComponent } from './add-salesman-class/add-salesman-class.component';
import { EditSalesmanClassComponent } from './edit-salesman-class/edit-salesman-class.component';

@Component({
  selector: 'app-salesman-class',
  templateUrl: './salesman-class.component.html',
  styleUrls: ['./salesman-class.component.scss']
})
export class SalesmanClassComponent implements OnInit {
  salesmanClass:any =[]
  loading:boolean = false
  constructor(
    // public queryVhweb:QueryVhweb,
    // public ordinaryAlgorithm:OrdinaryAlgorithm,
    public dialog: MatDialog,
    // public firebaseAuth: FirebaseAuth,
    // public ordinaryComponent: OrdinaryComponent,
  ) { }

  ngOnInit(): void {
    this.loading =true
    this.getSalesmanClasss()
  }

  getSalesmanClasss(){
    // this.queryVhweb.getSalesmanClasss()
    // .then((data)=>{
    //   this.salesmanClass = this.ordinaryAlgorithm.sortbyASC(data,'name') 
    //   this.loading =false
    // }, err=> {
    //   this.loading =false
    //   console.log(err)
    // })
  }

  openAddClass(){
    const dialogRef = this.dialog.open(AddSalesmanClassComponent, {
      width: '35%',
      maxWidth: '100%',
      disableClose:true
    });

    dialogRef.afterClosed().subscribe((result) => {
      if(result) this.getSalesmanClasss();
    });
  }
  openEditClass(item){
    const dialogRef = this.dialog.open(EditSalesmanClassComponent, {
      width: '35%',
      maxWidth: '100%',
      disableClose:true,
      data:item
    });

    dialogRef.afterClosed().subscribe((result) => {
      if(result) this.getSalesmanClasss();
    });
  }
  deleteClass(item){
    // this.queryVhweb.deleteSalesmanClass(item._id)
    // .then((bol)=>{
    //   this.getSalesmanClasss()
    //   this.ordinaryComponent.showToast(1500, "Đã xóa thành công")
    // },err=>  this.ordinaryComponent.showToast(1500, "Lỗi, vui lòng thử lại"))
  }

}
