import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { MaterialModule } from 'src/app/dealer/material.module';
import { AddSalesmanClassComponent } from './add-salesman-class/add-salesman-class.component';
import { EditSalesmanClassComponent } from './edit-salesman-class/edit-salesman-class.component';
import { SalesmanClassComponent } from './salesman-class.component';

const routes: Routes = [
  {
    path: '',
    component: SalesmanClassComponent,
  },
];

@NgModule({
  declarations: [SalesmanClassComponent, AddSalesmanClassComponent, EditSalesmanClassComponent],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    MaterialModule,
    FormsModule,
    RouterModule.forChild(routes),
  ]
})
export class SalesManClassModule { }
